<template>
  <div class="pull-log">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="token">
        <el-input v-model="form.token" placeholder="apptoken/devicetoken"></el-input>
      </el-form-item>
      <el-form-item label="pkgname">
        <el-input v-model="form.pkgName"></el-input>
      </el-form-item>
      <el-form-item label="开始时间">
        <el-date-picker
          v-model="form.startTime"
          type="date"
          placeholder="选择日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="结束时间">
        <el-date-picker
          v-model="form.endTime"
          type="date"
          placeholder="选择日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="日志类型">
        <el-select v-model="form.logType" placeholder="请选择日志内容">
          <el-option label="xlog" value="0"></el-option>
          <el-option label="csh" value="1"></el-option>
          <el-option label="all" value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

  import axios from 'axios';
  var moment = require('moment');
  var baseUrl = require('../../config').global.BASE_URL;

  export default {
    data() {
      return {
        form: {
          token: '',
          pkgName: '',
          startTime: '',
          endTime: '',
          logType: 'xlog'
        },
        result: ''
      }
    },
    methods: {
      onSubmit() {
        var me = this;
        var logType = '';
        var form = this.form;

        switch (form.logType) {
          case 'xlog':
            logType = 0;
            break;
          case 'csh':
            logType = 1;
            break;
          default:
            logType = 2;
        }

        axios({
          method: 'post',
          url: baseUrl + 'pullLog?t=' + Date.parse(new Date()) / 1000,
          data: {
            token: form.token,
            cmdid: 1,
            pkgname: form.pkgName,
            startdate: moment(form.startTime).format("YYYYMMDD"),
            enddate: moment(form.endTime).format("YYYYMMDD"),
            logtype: logType
          }
        }).then(function (response) {
            var msg = response.data.error_code == 0 ? '提交成功' : '提交失败,原因:' + response.data.error_msg;
            var type = response.data.error_code == 0 ? 'success' : 'error';
            me.$message({
              type: type,
              message: msg
            });
          })
          .catch(function (response) {
            console.log(response);
          });
      }
    }
  }
</script>

<style scoped>
  .pull-log {
    width: 400px;
    margin: 0 auto;
  }
</style>



