<template>
  <div class="query-cfg">
    <el-form :inline="true" ref="form" :model="form" label-width="100px">
      <el-form-item label="查询类型">
        <el-select v-model="form.queryType">
          <el-option label="pkgName" value="pkgName"></el-option>
          <el-option label="updateKey" value="updateKey"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.searchKey" class="query-input"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">查询</el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="cfgTable"
      border
      stripe
      class="cfg-table"
      max-height="1000px">
      <el-table-column
        fixed="left"
        prop="pkgName"
        label="pkgname"
        align="left"
        width="180">
      </el-table-column>
      <el-table-column
        fixed="left"
        prop="updateKey"
        label="updatekey"
        align="left"
        width="240">
      </el-table-column>
      <el-table-column
        prop="version"
        label="version"
        align="left"
        width="100">
      </el-table-column>
      <el-table-column
        prop="versionName"
        label="version_name"
        align="left"
        width="130">
      </el-table-column>
      <el-table-column
        prop="downloadUrl"
        label="download_url"
        align="left"
        width="240">
      </el-table-column>
      <el-table-column
        prop="sha256"
        label="sha256"
        align="left"
        width="240">
      </el-table-column>
      <el-table-column
        prop="size"
        label="size"
        align="left"
        width="120">
      </el-table-column>
      <el-table-column
        prop="type"
        label="type"
        align="left"
        width="70">
      </el-table-column>
      <el-table-column
        prop="grayRate"
        label="gray_rate"
        align="left"
        width="100">
      </el-table-column>
      <el-table-column
        prop="createTime"
        label="createTime"
        align="left"
        width="120">
      </el-table-column>
      <el-table-column
        prop="updateTime"
        label="updateTime"
        align="left"
        width="120">
      </el-table-column>
      <el-table-column
        prop="description"
        label="description"
        align="left"
        width="240">
      </el-table-column>
      <el-table-column
        fixed="right"
        prop="grayVer"
        label="gray_ver"
        align="left"
        width="100">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="100">
        <template scope="scope">
          <el-button
            @click.native.prevent="deleteCfg(scope.$index, cfgTable)"
            type="text"
            size="small">
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import axios from 'axios';
  var moment = require('moment');
  var baseUrl = require('../../config').global.BASE_URL;

  export default {
    data() {
      return {
        form: {
          queryType: 'pkgName',
          searchKey: ''
        },
        cfgTable: []
      }
    },
    methods: {
      onSubmit() {
        var me = this;
        var form = this.form;

        axios({
          method: 'post',
          url: baseUrl + 'queryConf?t=' + Date.parse(new Date()) / 1000,
          data: {
            fpkgname: form.queryType == 'pkgName' ? form.searchKey : '#',
            fupdate_key: form.queryType == 'updateKey' ? form.searchKey : '#'
          }
        }).then(function (response) {
            var tmpArray = [];
            response.data.list.forEach(function (elem, index, array) {
              tmpArray.push({
                pkgName: elem.fpkgname,
                updateKey: elem.fupdate_key,
                version: elem.fversion,
                versionName: elem.fverison_name,
                downloadUrl: elem.fdownload_url,
                sha256: elem.fsha256,
                size: elem.fsize,
                type: elem.ftype,
                grayVer: elem.fgray_ver,
                grayRate: elem.fgray_rate,
                description: elem.fdesc,
                createTime: moment(elem.fcreat_time).format("YYYY-MM-DD"),
                updateTime: moment(elem.fupdate_time).format("YYYY-MM-DD")
              });
            });
            me.cfgTable = tmpArray;
          })
          .catch(function (response) {
            console.log(response);
          });
      },
      deleteCfg(index, rows) {
        var me = this;
        this.$confirm('确认删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(function () {
            axios({
              method: 'post',
              url: baseUrl + 'UpdateConf?t=' + Date.parse(new Date()) / 1000,
              data: {
                type: 2, // 1 为添加/修改,2 为删除
                info: {
                  fpkgname: rows[index].pkgName,
                  fversion: rows[index].version,
                  fdownload_url: rows[index].downloadUrl,
                  fsha256: rows[index].sha256,
                  fsize: rows[index].size,
                  ftype: rows[index].type,
                  fcreat_time: rows[index].createTime,
                  fupdate_time: rows[index].updateTime,
                  fdesc: rows[index].description,
                  fupdate_key: rows[index].updateKey,
                  fverison_name: rows[index].versionName,
                  fgray_ver: rows[index].grayVer,
                  fgray_rate: rows[index].grayRate
                }
              }
            }).then(function (response) {
              if (response.data.error_code == 2) {
                me.$message({
                  type: 'success',
                  message: '删除成功'
                });
              }
              else {
                me.$message({
                  type: 'error',
                  message: '删除失败'
                });
              }
              })
          })
          .catch(function () {
            me.$message({
              type: 'info',
              message: '已取消删除'
            });
        });
      }
    }
  }
</script>

<style scoped>
  .query-input {
    width: 180px;
  }
  .cfg-table {
    width: 1200px;
    margin: 0 auto;
    margin-top: 50px;;
  }
</style>



