<template>
  <div class="update-cfg">
    <el-form ref="form" :model="form" label-width="100px">
      <el-form-item label="pkgname">
        <el-input v-model="form.pkgName" label-width=""></el-input>
      </el-form-item>
      <el-form-item label="updatekey">
        <el-input v-model="form.updateKey"></el-input>
      </el-form-item>
      <el-form-item label="开始时间">
        <el-date-picker
          v-model="form.createTime"
          type="date"
          placeholder="选择日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="更新时间">
        <el-date-picker
          v-model="form.updateTime"
          type="date"
          placeholder="选择日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="version">
        <el-input v-model="form.version"></el-input>
      </el-form-item>
      <el-form-item label="version_name">
        <el-input v-model="form.versionName"></el-input>
      </el-form-item>
      <el-form-item label="download_url">
        <el-input v-model="form.downloadUrl"></el-input>
      </el-form-item>
      <el-form-item label="sha256">
        <el-input v-model="form.sha256"></el-input>
      </el-form-item>
      <el-form-item label="size">
        <el-input v-model="form.size"></el-input>
      </el-form-item>
      <el-form-item label="type">
        <el-select v-model="form.type">
          <el-option label="不强制" value="不强制"></el-option>
          <el-option label="强制" value="强制"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="description">
        <el-input type="textarea" v-model="form.description"></el-input>
      </el-form-item>
      <el-form-item label="gray_ver">
        <el-select v-model="form.grayVer">
          <el-option label="白名单" value="白名单"></el-option>
          <el-option label="灰度" value="灰度"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="灰度比例" v-show="form.grayVer == '灰度'">
        <el-input v-model="form.grayRate" placeholder="0-100"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onPreview">配置预览</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import axios from 'axios';
  import Router from '../router';
  var baseUrl = require('../../config').global.BASE_URL;

  export default {
    data() {
      var reg = new RegExp('"', "g");
      return {
        form: {
          pkgName: window.sessionStorage.getItem('pkgName') == null ? '' : window.sessionStorage.getItem('pkgName').replace(reg, ''),
          updateKey: window.sessionStorage.getItem('updateKey') == null ? '' : window.sessionStorage.getItem('updateKey').replace(reg, ''),
          createTime: window.sessionStorage.getItem('createTime') == null ? '' : window.sessionStorage.getItem('createTime').replace(reg, ''),
          updateTime: window.sessionStorage.getItem('updateTime') == null ? '' : window.sessionStorage.getItem('updateTime').replace(reg, ''),
          version: window.sessionStorage.getItem('version') == null ? '' : window.sessionStorage.getItem('version').replace(reg, ''),
          versionName: window.sessionStorage.getItem('versionName') == null ? '' : window.sessionStorage.getItem('versionName').replace(reg, ''),
          downloadUrl: window.sessionStorage.getItem('downloadUrl') == null ? '' : window.sessionStorage.getItem('downloadUrl').replace(reg, ''),
          sha256: window.sessionStorage.getItem('sha256') == null ? '' : window.sessionStorage.getItem('sha256').replace(reg, ''),
          size: window.sessionStorage.getItem('size') == null ? '' : window.sessionStorage.getItem('size').replace(reg, ''),
          type: window.sessionStorage.getItem('type') == null ? '不强制' : window.sessionStorage.getItem('type').replace(reg, ''),
          description: window.sessionStorage.getItem('description') == null ? '' : window.sessionStorage.getItem('description').replace(reg, ''),
          grayVer: window.sessionStorage.getItem('grayVer') == null ? '白名单' : window.sessionStorage.getItem('grayVer').replace(reg, ''),
          whiteList: window.sessionStorage.getItem('whiteList') == null ? '' : window.sessionStorage.getItem('whiteList').replace(reg, ''),
          grayRate: window.sessionStorage.getItem('grayRate') == null ? '' : window.sessionStorage.getItem('grayRate').replace(reg, '')
        }
      }
    },
    beforeCreate() {
    },
    methods: {
      onPreview() {
        var updateCfgData = this.form;

        if (updateCfgData.grayVer === '白名单') {
          // 通过 pkgName 去请求白名单数据
          axios({
            method: 'post',
            url: baseUrl + 'queryWhiteList?t=' + Date.parse(new Date()) / 1000,
            data: {
              pkgname: updateCfgData.pkgName
            }
          }).then(function (response) {
            updateCfgData.whiteList = response.data.list;
            Object.keys(updateCfgData).forEach(function (key, index, array) {
              sessionStorage.setItem(key, JSON.stringify(updateCfgData[key]));
            });
            Router.push('previewCfg');
          })
            .catch(function (response) {
              console.log(response);
            });
        }
        else {
          Object.keys(updateCfgData).forEach(function (key, index, array) {
            sessionStorage.setItem(key, JSON.stringify(updateCfgData[key]));
          });
          Router.push('previewCfg');
        }
      }
    }
  }
</script>

<style scoped>
  .update-cfg {
    width: 300px;
    margin: 0 auto;
    margin-top: 50px;;
  }
</style>



