<template>
    <div class="update-push">
        <el-form ref="form" :model="form" label-width="100px">
            <el-form-item label="token">
                <el-input v-model="form.token" class="push-input"></el-input>
            </el-form-item>
            <el-form-item label="updatekey">
                <el-input v-model="form.updateKey" class="push-input"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onConfirm">确认</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import axios from 'axios';
    var baseUrl = require('../../config').global.BASE_URL;

    export default {
        data() {
            return {
                form: {
                    token: '',
                    updateKey: ''
                }
            }
        },
        methods: {
            onConfirm() {
              var me = this;
              var form = this.form;

              axios({
                method: 'post',
                url: baseUrl + 'updatePush?t=' + Date.parse(new Date()) / 1000,
                data: {
                  token: form.token,
                  cmdid: 2,
                  fupdate_key: form.updateKey
                }
              }).then(function (response) {
                  var type = response.data.err_code == 0 ? 'success' : 'error';
                  var msg = response.data.err_code == 0  ? '升级成功' : '升级失败,原因是:' + response.data.error_msg;
                  me.$message({
                    type: type,
                    message: msg
                  });
              })
                .catch(function (response) {
                  console.log(response);
                });
            }
        }
    }
</script>

<style scoped>
    .update-push {
        width: 420px;
        margin: 0 auto;
        margin-top: 50px;;
    }

    .push-input {
      width: 320px;
    }
</style>



