<template>
  <div class="white-list">
    <el-form ref="form" :inline="true" :model="form" label-width="100px">
      <el-form-item label="pkgname">
        <el-input v-model="form.pkgName" class="input-pkgName"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onQuery">查询</el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="whiteListTable"
      border
      stripe
      class="log-table"
      max-height="500px">
      <el-table-column
        prop="member"
        label="成员"
        align="left"
        width="600">
      </el-table-column>
      <el-table-column
        label="enable"
        align="left"
        width="100">
        <template scope="scope">
          <el-switch
            v-model="scope.row.enable"
            @change="modWhiteList('switch', scope.row.member, scope.row.enable)"
            on-color="#13ce66"
            off-color="#ff4949">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="100">
        <template scope="scope">
          <el-button
            @click.native.prevent="modWhiteList('delete', scope.row.member)"
            type="text"
            size="small">
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-form ref="form" :inline="true" :model="addForm" label-width="100px" class="add-member">
      <el-form-item label="deviceId">
        <el-input v-model="addForm.deviceId" class="input-pkgName"></el-input>
      </el-form-item>
      <el-form-item label="enable">
        <el-input v-model="addForm.enable" class="input-pkgName"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="modWhiteList('add', addForm.deviceId, addForm.enable)">添加白名单成员</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  import axios from 'axios';
  var baseUrl = require('../../config').global.BASE_URL;

  export default {
    data() {
      return {
        form: {
          pkgName: ''
        },
        addForm: {
          deviceId: ''
        },
        whiteListTable: []
      }
    },
    methods: {
      onQuery() {
        var me = this;
        var form = this.form;

        axios({
          method: 'post',
          url: baseUrl + 'queryWhiteList?t=' + Date.parse(new Date()) / 1000,
          data: {
            pkgname: form.pkgName
          }
        }).then(function (response) {
            var tmpArray = [];
            response.data.list.forEach(function (elem, index, array) {
              tmpArray.push({
                member: elem.deviceid,
                enable: elem.enable === 0 ? 0 : 1
              });
            });
            me.whiteListTable = tmpArray;
          })
          .catch(function (response) {
            console.log(response);
          });
      },
      modWhiteList(opt, member, enable) {
        var me = this;
        var type;
        switch (opt) {
          case 'add': type = 1; break;
          case 'delete': type = 2; break;
          default: type = !enable == true ? 0 : 1;
        }

        function sendReq() {
          axios({
            method: 'post',
            url: baseUrl + 'modWhiteList?t=' + Date.parse(new Date()) / 1000,
            data: {
              deviceid: member,
              type: type
            }
          }).then(function (response) {
              var msg = '';
              if (response.data.error_code === 2) { // 操作成功
                type = 'success';
                switch (opt) {
                  case 'add': msg = '添加成功'; break;
                  case 'delete': msg = '删除成功'; break;
                  default: msg = !enable == true ? '状态置 disable 成功' : '状态置 enable 成功';
                }
              }
              else { // 操作失败
                type = 'error';
                msg = '操作失败';
              }
              me.onQuery(); // 刷新下表格
              me.$message({
                type: type,
                message: msg
              });
            })
            .catch(function (response) {
              console.log(response);
            });
        }

        if (opt == 'delete') {
          this.$confirm('确认删除?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(function () { // 删除
            sendReq();
          }).catch(function () {
            me.$message({
              type: 'info',
              message: '取消删除'
            });
          });
        }
        else {
          sendReq();
        }
      }
    }
  }
</script>

<style scoped>
  .white-list {
    width: 802px;
    margin: 0 auto;
    margin-top: 50px;;
  }

  .add-member {
    margin-top: 30px;
  }

  .input-pkgName {
    width: 175px;
  }
</style>



