import Vue from 'vue';
import Router from 'vue-router';

const HomeTab = require('../components/HomeTab.vue');
const QueryInfo = require('../components/QueryInfo.vue');
const PullLog = require('../components/PullLog.vue');
const QueryLog = require('../components/QueryLog.vue');
const WhiteList = require('../components/WhiteList.vue');
const UpdateCfg = require('../components/UpdateCfg.vue');
const PreviewCfg = require('../components/PreviewCfg.vue');
const QueryCfg = require('../components/QueryCfg.vue');
const UpdatePush = require('../components/UpdatePush.vue');

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            components: {
                header: HomeTab,
                main: QueryInfo
            }
        },
        {
            path: '/queryInfo',
            components: {
                header: HomeTab,
                main: QueryInfo
            }
        },
        {
            path: '/pullLog',
            components: {
                header: HomeTab,
                main: PullLog
            }
        },
        {
            path: '/queryLog',
            components: {
                header: HomeTab,
                main: QueryLog
            }
        },
        {
            path: '/whiteList',
            components: {
                header: HomeTab,
                main: WhiteList
            }
        },
        {
            path: '/updateCfg',
            components: {
                header: HomeTab,
                main: UpdateCfg
            }
        },
        {
          path: '/previewCfg',
          components: {
            header: HomeTab,
            main: PreviewCfg
          }
        },
        {
            path: '/queryCfg',
            components: {
                header: HomeTab,
                main: QueryCfg
            }
        },
        {
            path: '/updatePush',
            components: {
                header: HomeTab,
                main: UpdatePush
            }
        }
    ]
});



