<template>
  <div class="pull-log">
    <el-form :inline="true" ref="form" :model="form" label-width="80px">
      <el-form-item label="apptoken">
        <el-input
          type="textarea"
          autosize
          class="input-token"
          placeholder="请输入apptoken"
          v-model="form.appToken">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">查询</el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="infoTable"
      border
      stripe
      class="info-table"
      max-height="1000px">
      <el-table-column
        fixed="left"
        prop="appToken"
        label="apptoken"
        align="left"
        width="310">
      </el-table-column>
      <el-table-column
        prop="uin"
        label="uin"
        align="left"
        width="90">
      </el-table-column>
      <el-table-column
        prop="token"
        label="token"
        align="left"
        width="540">
      </el-table-column>
      <el-table-column
        prop="sessionKey"
        label="sessionkey"
        align="left"
        width="180">
      </el-table-column>
      <el-table-column
        prop="createTime"
        label="create_time"
        align="left"
        width="120">
      </el-table-column>
      <el-table-column
        fixed="right"
        prop="updateTime"
        label="update_time"
        align="left"
        width="120">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import axios from 'axios';
//  var qs = require('qs');
  var moment = require('moment');

  var baseUrl = require('../../config').global.BASE_URL;

  export default {
    data() {
      return {
        form: {
          appToken: ''
        },
        infoTable: [{
          appToken: '暂无',
          uin: '暂无',
          token: '暂无',
          sessionKey: '暂无',
          createTime: '暂无',
          updateTime: '暂无'
        }]
      }
    },
    methods: {
      onSubmit() {
        var me = this;
        var appTokens = this.form.appToken.split(';');
        this.infoTable = []; // 先置空
        appTokens.forEach(function (item) {
          axios({
            method: 'post',
            url: baseUrl + 'QueryInfo?t=' + Date.parse(new Date()) / 1000,
            data: {
              token: item
            }
          }).then(function (response) {
              var result = response.data;
              var createTime = new Date(result.createtime * 1000);
              var updateTime = new Date(result.updatetime * 1000);
              if (result.token) {
                me.infoTable.push({
                  appToken: item,
                  uin: result.uin,
                  token: result.token,
                  sessionKey: result.sessionkey,
                  createTime: moment(createTime).format("YYYY-MM-DD"),
                  updateTime: moment(updateTime).format("YYYY-MM-DD")
                });
              }
            })
            .catch(function (response) {
              console.log(response);
            });
        });
      }
    }
  }
</script>

<style scoped>
  .pull-log, .info-table {
    width: 1200px;
    margin: 0 auto;
  }

  .input-token {
    width: 320px;
  }

  .info-table {
    margin-top: 50px;
  }
</style>



