<template>
  <div class="preview-cfg">
    <el-card class="box-card">
      <div class="text item">
        {{'pkgName: ' + form.pkgName }}
      </div>
      <div class="text item">
        {{'updateKey: ' + form.updateKey }}
      </div>
      <div class="text item">
        {{'createTime: ' + form.createTime }}
      </div>
      <div class="text item">
        {{'updateTime: ' + form.updateTime }}
      </div>
      <div class="text item">
        {{'version: ' + form.version }}
      </div>
      <div class="text item">
        {{'versionName: ' + form.versionName }}
      </div>
      <div class="text item">
        {{'downloadUrl: ' + form.downloadUrl }}
      </div>
      <div class="text item">
        {{'sha256: ' + form.sha256 }}
      </div>
      <div class="text item">
        {{'size: ' + form.size }}
      </div>
      <div class="text item">
        {{'type: ' + form.type }}
      </div>
      <div class="text item">
        {{'description: ' + form.description }}
      </div>
      <div class="text item" v-show="form.grayVer == '白名单'">
        {{'grayVer: 白名单' }}
      </div>
      <div class="text item" v-show="form.grayVer == '灰度'">
        {{'grayVer: 灰度' }}
      </div>
      <div class="text item" v-show="form.grayVer == '灰度'">
        {{'grayRate: ' + form.grayRate }}
      </div>
    </el-card>

    <el-table
      v-show="form.grayVer == '白名单'"
      :data="whiteListTable"
      border
      stripe
      class="whiteList-table"
      max-height="1000px">
      <el-table-column
        fixed="left"
        prop="deviceid"
        label="deviceid"
        align="left"
        width="280">
      </el-table-column>
      <el-table-column
        fixed="left"
        prop="enable"
        label="enable"
        align="left"
        width="100">
      </el-table-column>
    </el-table>
    <div class="btn-group">
      <router-link to="/updateCfg">
        <el-button type="primary" @click="onReturn">上一步</el-button>
      </router-link>
      <el-button type="primary" @click="onPreview">确认发布</el-button>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  import Router from '../router';
  var moment = require('moment');
  var baseUrl = require('../../config').global.BASE_URL;

  export default {
    beforeCreate() {
      console.log('1'); // data 未初始化
    },
    created() {
      var reg = new RegExp('"', "g");
      this.form = {
        pkgName: sessionStorage.getItem('pkgName').replace(reg, ''),
        updateKey: sessionStorage.getItem('updateKey').replace(reg, ''),
        createTime: moment(sessionStorage.getItem('createTime').replace(reg, '')).format("YYYYMMDD"),
        updateTime: moment(sessionStorage.getItem('updateTime').replace(reg, '')).format("YYYYMMDD"),
        version: sessionStorage.getItem('version').replace(reg, ''),
        versionName: sessionStorage.getItem('versionName').replace(reg, ''),
        downloadUrl: sessionStorage.getItem('downloadUrl').replace(reg, ''),
        sha256: sessionStorage.getItem('sha256').replace(reg, ''),
        size: sessionStorage.getItem('size').replace(reg, ''),
        type: sessionStorage.getItem('type').replace(reg, ''),
        description: sessionStorage.getItem('description').replace(reg, ''),
        grayVer: sessionStorage.getItem('grayVer').replace(reg, ''),
        grayRate: sessionStorage.getItem('grayRate').replace(reg, '')
      };
      this.whiteListTable = JSON.parse(sessionStorage.getItem('whiteList'));
    },
    data() {
      return {
        form: {
          pkgName: '',
          updateKey: '',
          version: '',
          versionName: '',
          downloadUrl: '',
          sha256: '',
          size: '',
          type: '',
          description: '',
          grayVer: '',
          grayRate: '',
          createTime: '',
          updateTime: ''
        },
        whiteListTable: []
      }
    },
    methods: {
      onReturn() {
        Router.push('updateCfg');
      },
      onPreview() {
        var me = this;
        var form = this.form;
        axios({
          method: 'post',
          url: baseUrl + 'updateConf?t=' + Date.parse(new Date()) / 1000,
          data: {
            type: '1', // 1 为添加/修改,2 为删除
            info: {
              fpkgname: form.pkgName,
              fversion: form.version,
              fdownload_url: form.downloadUrl,
              fsha256: form.sha256,
              fsize: form.size,
              ftype: form.type,
              fcreat_time: form.createTime,
              fdesc: form.description,
              fupdate_key: form.updateKey,
              fupdate_time: form.updateTime,
              fverison_name: form.versionName,
              fgray_ver: form.grayVer,
              fgray_rate: form.grayRate
            }
          }
        }).then(function (response) {
            var type = response.data.error_code == 2 ? 'success' : 'error';
            var msg = response.data.error_code == 2 ? '发布成功' : '发布失败';
            me.$message({
              type: type,
              message: msg
            });
          })
          .catch(function (response) {
            console.log(response);
          });
      }
    }
  }
</script>

<style scoped>
  .preview-cfg {
    width: 600px;
    margin: 0 auto;
  }

  .text {
    font-size: 14px;
    text-align: left;
  }

  .item {
    padding: 10px 0;
  }

  .box-card {
    width: 600px;
    margin-bottom: 30px;
  }

  .whiteList-table {
    width: 381px;
    margin: 0 auto;
    margin-bottom: 30px;
  }

  .btn-group {
    margin: 0 auto;
    margin-bottom: 30px;
  }
</style>



