<template>
  <div class="pull-log">
    <el-form :inline="true" ref="form" :model="form" label-width="84px">
      <el-form-item label="token">
        <el-input v-model="form.token" class="app-token" placeholder="apptoken/devicetoken"></el-input>
      </el-form-item>
      <el-form-item label="搜索关键字">
        <el-input v-model="form.pkgName" placeholder="pkgname"></el-input>
      </el-form-item>
      <el-form-item label="开始时间">
        <el-date-picker
          v-model="form.startTime"
          type="date"
          placeholder="选择日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="结束时间">
        <el-date-picker
          v-model="form.endTime"
          type="date"
          placeholder="选择日期">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="日志类型">
        <el-select v-model="form.logType">
          <el-option label="xlog" value="xlog"></el-option>
          <el-option label="csh" value="csh"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">查询</el-button>
      </el-form-item>
    </el-form>
    <el-table
      :data="logTable"
      border
      stripe
      class="log-table"
      max-height="500px">
      <el-table-column
        prop="name"
        label="日志文件名"
        align="left"
        width="300">
      </el-table-column>
      <el-table-column
        prop="date"
        label="文件时间"
        align="left"
        width="240">
      </el-table-column>
      <el-table-column
        prop="size"
        label="文件大小"
        align="left"
        width="240">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="240">
        <template scope="scope">
          <el-button
            @click.native.prevent="download(scope.$index, logTable)"
            type="text"
            size="small">
            下载
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import axios from 'axios';
  var moment = require('moment');
  var baseUrl = require('../../config').global.BASE_URL;

  export default {
    data() {
      return {
        form: {
          token: '',
          pkgName: '',
          startTime: '',
          endTime: '',
          logType: 'xlog'
        },
        logTable: []
      }
    },
    methods: {
      onSubmit() {
        var me = this;
        var form = this.form;

        axios({
          method: 'post',
          url: baseUrl + 'SearchLogFile?t=' + Date.parse(new Date()) / 1000,
          data: {
            token: form.token,
            searchkey: form.pkgName,
            starttime: moment(form.startTime).format("YYYYMMDD"),
            endtime: moment(form.endTime).format("YYYYMMDD"),
            logtype: form.logType
          }
        }).then(function (response) {
            var tmpArray = [];
            response.data.filelist.forEach(function (elem, index, array) {
              tmpArray.push({
                name: elem.filename,
                date: elem.uploadtime,
                size: elem.filesize
              });
            });
            me.logTable = tmpArray;
          })
          .catch(function (response) {
            console.log(response);
          });
      },
      download (index, rows) {
        var fileName = rows[index].name;
        window.location.href = baseUrl + 'getlog?t=' + Date.parse(new Date()) / 1000 + '&f=' + fileName;
      }
    }
  }
</script>

<style scoped>
  .log-table {
    width: 1021px;
    margin: 0 auto;
    margin-top: 50px;;
  }

  .app-token {
    width: 310px;
  }
</style>



